%site-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 12rem;
  margin: 0 auto;
  background-color: var(--color-gray-700);
  border-radius: 0.8rem;
  font-size: var(--font-size-16);
  color: #fff;
  text-align: center;
  padding: 0;
  height: 4.8rem;
  box-sizing: border-box;
  border: 0;
  position: relative;
  &.disable {
    pointer-events: none;
    background-color: var(--color-gray-100);
  }
  &.locked {
    pointer-events: none;
    background-color: var(--color-gray-100);
    &::after {
      width: 2.4rem;
      height: 2.4rem;
      position: absolute;
      right: 0;
      top: 50%;
      border-radius: 50%;
      background: var(--color-gray-200) url('../images/icon-locked.svg') no-repeat center 45%;
      background-size: 1.2rem;
      transform: translate3d(40%,-50%,0);
      content: "";
    }
  }
}

%site-block-ui {
  background-color: #fff;
  border-radius: 1.6rem;
  padding: 2rem;
  box-shadow: 0 0.4rem 1.2rem rgba(0,0,0,0.08);
}