  0%, 100% {
    transform: rotate3d(0, 1, 0, 15deg); }
  50% {
    transform: rotate3d(0, 1, 0, -15deg); } }

@keyframes gradientAnimation {
  0% {
    background-position: 0% 100%;
    opacity: 1; }
  30%,
  80% {
    opacity: 1; }
  100% {
    background-position: 0% 0%;
    opacity: 1; } }
