.popup-ui {
    $self: &;
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    pointer-events: none;
    transition: all 400ms ease-in-out;
    opacity: 0;
    z-index: 300;
    &.active {
        opacity: 1;
        pointer-events: auto;
    }
    &__inner {
        width: 100%;
        height: 100%;
        background-color: rgba(0,0,0,0.8);
        display: flex;
        align-items: center;
        justify-content: center;
    }
    &__icon {
        width: 5.6rem;
        height: 5.6rem;
        display: block;
        margin: 0 auto 1.6rem;
        &.incorrect {
            background: transparent url('../images/icon-incorrect.svg') no-repeat 0 0;
            background-size: cover;
        }
        &.correct {
            background: transparent url('../images/icon-correct.svg') no-repeat 0 0;
            background-size: cover;
        }
        &.info {
            background: transparent url('../images/icon-explain.svg') no-repeat 0 0;
            background-size: cover;
        }
    }
    &__content {
        border-radius: 1.6rem;
        max-width: 80rem;
        background-color: #fff;
        padding: 2rem 0;
        box-sizing: border-box;
        position: relative;
        box-shadow: 0 0 5rem rgba(0,0,0,0.15);
        @include susy-breakpoint($s-320, $c8) {
            width: calc(100% - 4.8rem);
            flex-basis: calc(100% - 4.8rem);
        }
        p {
            font-size: var(--font-size-14);
            line-height: 1.6;
            text-align: center;
            color: var(--color-gray-500);
            padding: 0 1.6rem;
        }
    }
    &__btn-chose {
        @extend %site-btn;
    }
    &__btn-close {
        width: 3rem;
        height: 3rem;
        position: absolute;
        right: 1.6rem;
        top: 1.6rem;
        background: transparent url('../images/icon-close.svg') no-repeat 0 0;
        background-size: cover;
        padding: 0;
        border: 0;
    }
    &__status {
        margin-bottom: 1.6rem;
        h4 {
            font-size: var(--font-size-16);
            color: var(--color-gray-500);
            text-align: center;
            line-height: 1.6;
            strong {
                display: block;
                color: var(--color-green-500);
            }
        }
    }
    &__title {
        font-size: var(--font-size-16);
        color: var(--color-gray-500);
        text-align: center;
        line-height: 1.6;
        margin-bottom: 0.8rem;
    }
    &__avatar {
        width: 10.8rem;
        height: 10.8rem;
        margin: 0 auto 1.6rem;
        img {
            max-width: 100%;
            display: block;
            margin: 0 auto;
        }
    }
    &__btn-back {
        @extend %site-btn;
    }
    &__medal-img {
        max-width: 12rem;
        display: block;
        margin: 0 auto 0.8rem;
    }
}