.section-medal {
    $self: &;
    padding-bottom: 6.6rem;
    &__profile {
        @extend %site-block-ui;
        margin-bottom: 2.4rem;
    }
    &__avatar {
        margin: 0 auto 1.6rem;
        position: relative;
        padding-top: 1.6rem;
        &::before {
            content: "";
            position: absolute;
            left: -2rem;
            top: -2rem;
            height: 12rem;
            width: calc(100% + 4rem);
            z-index: 0;
            background: linear-gradient(0deg, var(--color-orange-500) 0%, var(--color-orange-400) 100%);
            border-radius: 1.6rem 1.6rem 0 0;
        }
        img {
            width: 12rem;
            display: block;
            margin: 0 auto;
            position: relative;
            z-index: 10;
            border: solid 0.4rem #fff;
            border-radius: 50%;
        }
    }
    &__name {
        margin-bottom: 2rem;
        border-bottom: 0.1rem dashed var(--color-gray-70);
        padding-bottom: 2rem;
        h2 {
            font-size: var(--font-size-32);
            color: var(--color-gray-700);
            text-align: center;
            margin-bottom: 0.8rem;
        }
        h3 {
            font-size: var(--font-size-14);
            color: var(--color-gray-500);
            text-align: center;
        }
    }
    &__block {
        & > h4 {
            font-size: var(--font-size-14);
            color: var(--color-gray-500);
            line-height: 1.6;
            text-align: center;
            margin-bottom: 1.6rem;
        }
        &:nth-child(1) {
            border-right: 0.1rem dashed var(--color-gray-70);
        }
    }
    &__medal-number {
        font-size: var(--font-size-96);
        color: var(--color-orange-500);
        text-align: center;
        font-weight: 300;
        transform: translate3d(0,0,0);
    }
    &__status {
        display: grid;
        grid-template-columns: repeat(2, minmax(0, 1fr));
        padding-bottom: 1.6rem;
    }
    &__circle {
        width: 10.4rem;
        height: 10.4rem;
        transform: rotate(-90deg);
    }
    &__circle-background {
        fill: none;
        stroke: var(--color-gray-70);
        stroke-width: 0.15rem;
    }
    &__circle-progress {
        fill: none;
        stroke: var(--color-orange-500);
        stroke-dasharray: 100 100;
        stroke-dashoffset: 100;
        stroke-linecap: round;
        stroke-width: 0.15rem;
        transition: stroke-dashoffset 2s ease-in-out;
    }
    &__circle-title {
        position: absolute;
        height: 10.4rem;
        width: 10.4rem;
        top: 50%;
        left: 50%;
        transform: translate3d(-50%,-50%,0);
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 3.2rem;
        color: var(--color-orange-500);
    }
    &__process {
        position: relative;
        height: 10.4rem;
        width: 10.4rem;
        margin: 0 auto;
    }
    &__list {
        @extend %site-block-ui;
        &:not(:last-child) {
            margin-bottom: 2.4rem;
        }
    }
    &__header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 2rem;
        border-bottom: 0.1rem dashed var(--color-gray-70);
        padding-bottom: 2rem;
    }
    &__btn-info {
        margin-left: 0;
        margin-right: auto;
        width: 1.8rem;
        height: 1.8rem;
        background: transparent url('../images/icon-info-grey.svg') no-repeat 0 0;
        background-size: cover;
        font-size: 0;
        border: 0;
    }
    &__title {
        font-size: var(--font-size-16);
        color: var(--color-gray-500);
        margin-right: 0.4rem;
    }
    &__progress {
        span {
            font-size: var(--font-size-16);
            color: var(--color-gray-500);
            font-weight: 900;
            &:nth-child(1) {
                &::after {
                    content: "/";
                    padding-left: 0.8rem;
                    font-size: var(--font-size-16);
                    color: var(--color-gray-500);
                    padding-right: 0.4rem;
                }
            }
        }
    }
    &__medal-wrap {
        display: flex;
        overflow-x: scroll;
        overflow-y: hidden;
        gap: 1.6rem;
        -webkit-overflow-scrolling: touch;
        padding-bottom: 1.6rem;
        &::-webkit-scrollbar {
            width: 0.6rem;
            height: 0.6rem;
        }
        &::-webkit-scrollbar-track {
            background-color: var(--color-gray-70);
            border-radius: 0.4rem;
        }
        &::-webkit-scrollbar-thumb {
            background-color: var(--color-gray-100);
            border-radius: 0.4rem;
        }
    }
    &__medal-item {
        position: relative;
        & > p {
            text-align: center;
            font-size: var(--font-size-12);
            color: var(--color-gray-200);
            padding-top: 0.8rem;
        }
        &.active {
            perspective: 12rem;
            #{$self}__medal {
                transform: rotate3d(0, 1, 0, 15deg);
                animation: rotateAnimation;
                animation-timing-function: linear;
                animation-duration: 3000ms;
                animation-iteration-count: infinite;
                animation-direction: normal;
                animation-fill-mode: both;
                img {
                    filter: grayscale(0);
                    opacity: 1;
                }
                &::after {
                    display: block;
                }
            }
        }
    }
    &__medal {
        position: relative;
        width: 8.8rem;
        height: 8.8rem;
        img {
            width: 8.8rem;
            height: auto;
            display: block;
            margin: 0 auto 0.8rem;
            filter: grayscale(1);
            opacity: 0.4;
        }
        &::after {
            content: "";
            position: absolute;
            left: 0;
            top: 0;
            width: 8.8rem;
            height: 8.8rem;
            background: linear-gradient(rgba(255,255,255,0) 45%, rgba(255,255,255,0.7) 60%, rgba(255,255,255,0) 75%);
            background-size: 400% 400%;
            background-position: 0% 100%;
            transform: rotate(30deg);
            animation: gradientAnimation;
            animation-timing-function: linear;
            animation-duration: 3000ms;
            animation-iteration-count: infinite;
            animation-direction: normal;
            animation-fill-mode: both;
            mix-blend-mode: overlay;
            opacity: 1;
            display: none;
        }
    }
}

@keyframes rotateAnimation {
    0%, 100% {
        transform: rotate3d(0, 1, 0, 15deg);
    }
    50% {
        transform: rotate3d(0, 1, 0, -15deg);
    }
}

@keyframes gradientAnimation {
    0% {
        background-position: 0% 100%;
        opacity: 1;
    }
    30%,
    80% {
        opacity: 1;
    }
    100% {
        background-position: 0% 0%;
        opacity: 1;
    }
}
