.section-exam {
    &__exam {
        @extend %site-block-ui;
        margin-bottom: 2.4rem;
    }
    &__title {
        font-size: var(--font-size-16);
        color: var(--color-gray-500);
        line-height: 1.6;
        position: relative;
        padding-left: 2.8rem;
        border-bottom: 0.1rem dashed var(--color-gray-70);
        padding-bottom: 1.6rem;
        margin-bottom: 1.6rem;
        &::before {
            content: "Q";
            width: 2.4rem;
            height: 2.4rem;
            background-color: var(--color-gray-500);
            border-radius: 50%;
            font-size: var(--font-size-14);
            position: absolute;
            left: 0;
            top: 0;
            color: #fff;
            text-align: center;
            line-height: 2.4rem;
        }
    }
    &__answer-wrap {
        display: flex;
        gap: 1.2rem;
        flex-wrap: wrap;
    }
    &__btn-answer {
        box-sizing: border-box;
        padding: 1.2rem;
        flex-basis: 100%;
        background-color: var(--color-gray-70);
        border-radius: 0.8rem;
        display: block;
        border: 0;
        font-size: var(--font-size-16);
        text-align: left;
        padding-left: 6rem;
        color: var(--color-gray-500);
        position: relative;
        transition: all 100ms ease;
        &.incorrect {
            background-color: var(--color-red-500);
            color: #fff;
            &::before {
                background-color: var(--color-red-700);
            }
        }
        &.correct {
            background-color: var(--color-green-500);
            color: #fff;
            &::before {
                background-color: var(--color-green-700);
            }
        }
        &::before {
            transition: all 100ms ease;
            border-radius: 0.8rem 0 0 0.8rem;
            width: 4.8rem;
            height: 4.8rem;
            background-color: var(--color-gray-100);
            font-size: var(--font-size-16);
            position: absolute;
            left: 0;
            top: 0;
            color: #fff;
            text-align: center;
            line-height: 4.8rem;
            font-weight: 700;
        }
        &:nth-child(1) {
            &::before {
                content: "A";
            }
        }
        &:nth-child(2) {
            &::before {
                content: "B";
            }
        }
        &:nth-child(3) {
            &::before {
                content: "C";
            }
        }
        &:nth-child(4) {
            &::before {
                content: "D";
            }
        }
    }
    &__btn-back {
        @extend %site-btn;
    }
}