:root {
    font-size: 10px;
    --color-bg: #F7F7F7;
    --color-green-500: #A2CE42;
    --color-green-700: #8ebc2b;
    --color-orange-400: #ffaa37;
    --color-orange-500: #f69919;
    --color-red-500: #E26767;
    --color-red-700: #d24d4d;
    --color-blue-500: #4EACE9;
    --color-gray-50: #FAFAFA;
    --color-gray-70: #eeeeee;
    --color-gray-90: #dddddd;
    --color-gray-100: #cccccc;
    --color-gray-200: #999999;
    --color-gray-500: #666666;
    --color-gray-700: #333333;
    --font-size-96: 9.6rem;
    --font-size-32: 3.2rem;
    --font-size-20: 2rem;
    --font-size-16: 1.6rem;
    --font-size-14: 1.4rem;
    --font-size-12: 1.2rem;
}

html {
    font-family: "Open Sans","Noto Sans TC","微軟正黑體","Microsoft JhengHei";
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    &.is-fixed {
        overflow: hidden;
    }
}

body {
    background-color: var(--color-bg);
}

.site-main {
    max-width: 48rem;
    margin: 0 auto;
}

.wrap {
    padding: 3.2rem 2rem;
}

.site-header {
    display: flex;
    justify-content: center;
    margin-bottom: 3.2rem;
    h1 {
        border: 0.2rem solid var(--color-green-500);
        border-radius: 0.8rem;
        padding: 0.8rem;
        color: var(--color-green-500);
        font-size: var(--font-size-16);
        line-height: 1;
    }
    &.medal {
        h1 {
            color: var(--color-orange-500);
            border-color: var(--color-orange-500);
        }
    }
}