.section-task {
    &__task {
        @extend %site-block-ui;
        &:not(:last-child) {
            margin-bottom: 2.4rem;
        }
    }
    &__header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-bottom: 0.1rem dashed var(--color-gray-70);
        padding-bottom: 0.8rem;
        margin-bottom: 0.8rem;
        h2 {
            font-size: var(--font-size-20);
            color: var(--color-gray-500);
        }
    }
    &__status {
        vertical-align: middle;
        line-height: normal;
        display: flex;
        padding: 0 1.2rem;
        border-radius: 2rem;
        font-size: var(--font-size-14);
        color: #fff;
        align-items: center;
        height: 2.8rem;
        line-height: 2.8rem;
        &.success {
            background-color: var(--color-green-500);
        }
        &.unsuccess {
            background-color: var(--color-red-500);
        }
    }
    &__row {
        &:not(:last-child) {
            border-bottom: 0.1rem dashed var(--color-gray-70);
            padding-bottom: 1.2rem;
            margin-bottom: 1.2rem;
        }
        p {
            font-size: var(--font-size-14);
            color: var(--color-gray-500);
            line-height: 1.6;
        }
    }
    &__result {
        position: relative;
        font-size: var(--font-size-14);
        padding-left: 2rem;
        line-height: 1.6;
        &::before {
            width: 1.6rem;
            height: 1.6rem;
            position: absolute;
            content: "";
            left: 0;
            top: 0.3rem;
        }
        &.success {
            color: var(--color-green-500);
            &::before {
                background: transparent url('../images/icon-complete.svg') no-repeat 0 0;
                background-size: 1.6rem;
            }
        }
        &.unsuccess {
            color: var(--color-green-500);
            &::before {
                background: transparent url('../images/icon-info.svg') no-repeat 0 0;
                background-size: 1.6rem;
            }
        }
    }
    &__btn-challenge {
        @extend %site-btn;
    }
}